import uniStatusBar from '../../components/uni-status-bar/uni-status-bar.vue'
import OtherLogin from '../../components/home/other-login.vue'

export default {
	components: {
		uniStatusBar,
		OtherLogin
	},
	data() {
		return {
			status: false,   // false: 账号密码登录  true：手机验证码登录
			// 账号密码登录
			username: '',
			password: '',
			// 手机验证码登录
			phone: '',
			checknum: '',
			loading: false,
			disable: true,
			// 验证码
			codetime: 0,
		}
	},
	watch: {
		username(val) {
			this.onBtnChange()
		},
		password(val) {
			this.onBtnChange()
		},
		phone(val) {
			this.onBtnChange()
		},
		checknum(val) {
			this.onBtnChange()
		}
	},
	methods: {
		submit() {
			if(this.status) {
				// 手机验证码登录
				if(!this.isPhone()) {
					uni.showToast({
						title: '请填写正确的手机号',
						icon: 'none'
					});
					return;
				}
			}else {
				// 账号密码登录
			}
		},
		// 初始化表单
		initInput() {
			this.username = '';
			this.password = '';
			this.phone = '';
			this.checknum = '';
		},
		// 改变按钮状态
		onBtnChange() {
			if(!this.status) {
				// 手机验证码登录
				if(this.username && this.password) {
					this.disable = false;
					return 
				}
			}else {
				// 账号密码登录
				if(this.phone && this.checknum) {
					this.disable = false;
					return 
				}
			}
			this.disable = true;
		},
		// 登录方式切换
		changeStatus() {
			this.initInput();
			this.status = !this.status;
		},
		// 获取验证码
		getCheckNum() {
			if(!this.phone) {
				uni.showToast({
					title: '请填写手机号',
					icon: 'none'
				});
				return;
			}
			if(!this.isPhone()) {
				uni.showToast({
					title: '请填写正确的手机号',
					icon: 'none'
				});
				return;
			}
			if(this.codetime>0) {
				uni.showToast({
					title: '不能重复获取验证码',
					icon: 'none'
				});
				return;
			}
			// 发送请求，发送验证码，请求成功，开启倒计时
			this.codetime = 10;
			let timer = setInterval(() => {
				this.codetime --
				if(this.codetime<1) {
					clearInterval(timer)
				}
			}, 1000)
		},
		// 验证手机号码
		isPhone(){
			let mPattern = /^1[34578]\d{9}$/; 
			return mPattern.test(this.phone);
		},
		back() {
			uni.navigateBack({
				delta: 1
			});
		}
	}
}
